import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, Select } from 'antd';
import { typeDisciplines } from 'dump/data';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    level: '',
    type: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

const { Option } = Select;

@Form.create({ name: 'formTypeDisciplines' })
class FormTypeDisciplinesPage extends Component {
  state = {
    typeDisciplinesId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ typeDisciplinesId: match.params.id });
      this.getTypeDisciplines(match.params.id);
    }
  };

  getTypeDisciplines = code => {
    const typeMap = typeDisciplines.filter(object => object.type === code)[0];
    const initialValue = handleInitialValue(typeMap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { typeDisciplinesId } = this.state;
    return (
      <Card title={typeDisciplinesId ? 'Cập nhật xử lý kỹ luật' : 'Thêm xử lý kỹ luật'}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Loại sử lý" {...formItemLayout}>
              {getFieldDecorator('type', {
                initialValue: initialValue.type,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}

            </Form.Item>
            <Form.Item {...formItemLayout} label="Mức xử lý" hasFeedback>
              {getFieldDecorator('level', {
                initialValue: initialValue.level,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <Select>
                  <Option value="1">1</Option>
                  <Option value="2">2</Option>
                </Select>)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card >
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormTypeDisciplinesPage {...props} />
  </Page>
));



